.Navlinks {
    list-style:none;
    padding: 5px 10px;
    display: flex;
    box-sizing: border-box;
    border-bottom: 2px solid transparent;
    
}
.Navlinks a{
    color: #000;
    text-decoration: none;
    font-weight: bold;
    padding-bottom: 5px;
}
.Navlinks a:hover, .Navlinks a:active {
    color: #6FABE8 !important;
    text-decoration: none;
}
.active {
    border-bottom: 2px solid #6FABE8;
    color: black !important;
}
/*blueviolet*/
.help{width: 40px;
    margin-left: auto;
    margin-right: 100px;
    cursor: pointer;
    font-weight: 600;
    color: grey;
}

.help ul{
    width: 130px;
    position: absolute;
    display: none;
    right:60px;
    padding: 10px 10px 30px 10px;
    margin: 10px;
    color: #000;
    background: aliceblue;
    text-align: right;
    border-radius: 10px;

}
.help ul div{
border-top: 1px solid #6FABE8;
padding-top: 10px;
}
.help ul div li {
    font-size: .9rem;
    cursor: pointer;
    list-style: none;
    font-weight: 600;
    color: grey;
}
.help:hover{
    color: #6FABE8;
    font-weight: 600;
}
.help:hover ul{display: block;
}
.help ul div li:hover{
    color: #6FABE8;
}