header{
    background-color: aliceblue;
    padding: 60px 0 0 0px;
    margin: 0 !important;
}
.NavbarFirst {
    background-color: transparent !important;
    display: flex;
    list-style: none;
    flex-flow: row;
    width: 100%;
    height: 100px;
    padding: 0 5%;
    
}
.jPNCrY{position: absolute;top: 0;}
.NavbarFirst h2{
    display: flex;
    float: left;
    width: 40%;
    margin: 25px 0 0 25px;
}
.NavbarFirst nav{
    float: right;
    display: flex;
    box-sizing: border-box;
    text-align: right;
     background-color: transparent;/*rgba(102, 51, 153, .7); */
     box-shadow: 1px 1px 8px inset transparent;
    width: 175%;
    padding: 0px 20px 10px 0;
    margin: 0px;
    right: 0;
    position: inherit;
    height: 85px;
    border-radius: 0 0 0 10px;
    color: #000;
}
.NavbarFirst nav form{
    display: flex;
    height: 50%;
    margin: auto;
    width:50%;    
}
.NavbarFirst nav ul{
    list-style: none;
    display: flex;
    height: 50%;
    padding-top: 5px;
    margin: auto 0;
}
.NavbarFirst nav ul li{
    color: #000;
    display: flex;
    box-sizing: border-box;
    padding: 0 10px;
}
.NavbarFirst nav ul li a{
    color: #000;
    text-decoration: none;
    font-weight: 600;
}
.NavbarSecondul {
    background-color: transparent !important;
    display: flex;
    list-style: none;
    flex-flow: row;
    width: 100%;
}
.NavbarSecond{border-top:1px solid lightgrey;}
