.logo{
    width: 40px;
    height: 40px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin: 0 10px;
}


@media (max-width:1200px){
    .NavbarSecondul{
        display: none;
    }
    .NavbarFirst{padding: 0 0%;}
    .NavbarFirst h2{font-size: 1.5em;margin-left: 5px;}
    .NavbarFirst nav form {
        display: none;
    }
    .NavbarFirst nav{
        background: transparent;
    }
    .NavbarFirst nav ul li a{
        color: #000;
    }
    .logo{width: 32px;height: 32px;}
   
}

