.pageViewer{ 
    display: block;
    padding: 0 20%;
    background-color: rgba(220, 220, 220, alpha) !important;
    margin: 0 !important;
}
.ad {
    min-width: 200px;
    min-height: 500px;
    display: inline;
    font-size: 3em;
}
.add {
    padding: 80px 0 200px 0;
}
.add p,.add h3{font-weight: 650 !important;}
.kjds{font-size: 2em !important;line-height: 35px !important;}
.sat{width: 150px;height: 50px;margin: 10px;}

.imgside{
    width: 100%;
    min-height: 200px;
    margin: auto;
    height: 80% !important;

}
.mik{font-size: 1.4em;font-weight: 600;padding: 30px 10px;}

.st1{margin-top:100px;}
.bgfirst{width:100vw !important;max-height: 80vh;color: #fff;padding: 0px 10%;padding-top: 100px;
    background: url('../../assets/images/home-hero-gradient.svg');}

@media (max-width: 1200px){
    .pageViewer{
        padding: 0 0px;
    }
    .imgside{width: 80%;max-height: 200px;}
    .textg{margin-top: 250px;}
}



