.modal-body{
    border: none;
        overflow-y: scroll;
        max-height: 550px;
        height: 40%;
        
}
#connect-WALLET_LINK {
    width: 100% !important;
    display: block;
    padding: 10px;
    text-align: left !important;
    color: #fff !important;
    margin: auto !important;
}

#connect-WALLET_LINK img {
    position: absolute !important;
    max-width: 40px;
    width: 40px;
    height: 40px;
    display: inline-block;
    /* float: right; */
    left: 10%;
    margin-top: -25px;
    border-radius: 15px;
}


#connect-WALLET_LINK .irnmqJ,
#connect-WALLET_LINK .hKWpfm {
    display: inline-block;
    text-align: left;
}

#connect-WALLET_LINK .irnmqJ {
    padding: 10px 0 10px 80px;
}

input,
textarea {
    background: #2E343B !important;
    color: #fff !important;
}

.tab-content button,
.tab-content textarea {
    font-size: .9em;
}

/* .nav-tabs {} */

.tab-content button {
    z-index: 1000px !important;
}

.mat {
    margin: 100px auto;
}

.mat a {
    display: inline-block;
    padding: 50px 40px;
    background: #56042C;
    color: #fff;
    margin: 20px !important;
    transition: 1s ease;
}

.mat a:hover {
    font-weight: 800;
}

/* popup connect wallet
--------------------------------------------------*/
.modal-open {
    padding-right: 0 !important;
}

.modal {
    background: rgba(4, 11, 17, 0.7);
    overflow: hidden;
    width: 100vw;
    backdrop-filter: blur(10px);
    z-index: 99999;
}
   .modal-dialog {
        max-width: 629px;
        overflow: hidden;
        margin: 50px auto;
   }
       .modal-content {
            background: var(--primary-color6);
            border: unset;
            text-align: center;
       }
           .modal-body {
                padding: 42px 60px;
                position: relative;
           }
                .btn-close {
                    position: absolute;
                    top: 17px;
                    right: 21px;
                    color: var(--primary-color20);
                    font-size: 28px;
                }

             .btn-close h3 {
                    color: var(--primary-color3);
                    text-align: center;
                    margin-bottom: 10px;
                }

               .sub-heading {
                    font-size: 18px;
                    line-height: 28px;
                    color: var(--primary-color);
                    margin-bottom: 41px;
                    padding: 0 33px;
                }

                           .sub-heading  .bottom {
                    font-size: 16px;
                    line-height: 26px;
                    padding: 0 30px;
                    color: var(--primary-color13);
                }
                          .sub-heading .bottom  a {
                        color: var(--primary-color5);
                    }
                

                .tf-wallet {
                    position: relative;
                    background-color: var(--primary-color24);
                    /* @include flex(center, left); */
                    padding: 23px 27px 19px 33px;
                    margin-bottom: 18px;
                }
                                        .tf-wallet .image {
                        margin-right: 20px;
                    }

                                        .tf-wallet .content {
                        text-align: left;
                    }
                        .title {
                            font-weight: 500;
                            font-size: 22px;
                            line-height: 120%;
                            color: var(--primary-color3);
                            margin-bottom: 2px;
                        }

                        .sub {
                            color: #888B8E;
                            font-size: 16px;
                            line-height: 26px;
                        }
                    
                   .mb30 {
                        margin-bottom: 30px;
                    }

                   .mb30::after {
                        content: '\f054';
                        font-family: 'Font Awesome 5 Pro';
                        position: absolute;
                        top: 50%;
                        right: 27px;
                        transform: translateY(-50%);
                        color: #C2C3C5;
        }
            

.inpo {
    border: none;
    background: #fff;
    border-radius: 10px;
    padding: 10px 20px;
    text-align: left;
    width: 100% !important;
    min-width: 350px;
    margin: auto;
}

.popup {
    background: rgba(50, 50, 50, .8);
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    padding-top: 200px;
}

.ih {
    background: #fff;
    color: #E74041;
    transition: 1s ease;
}

.ih:hover {
    background: #130912;
}

@media (max-width: 800px) {
    .nav-tabs li {
        padding: 5px !important;
        font-size: .8em !important;
    }
}