.Button{
    padding: 10px;
    font-weight: 600;
    outline: none;
    color: #fff;
    border: none;
    cursor: pointer;
}
.noBack{
    background: transparent !important;
    margin: 0 5px;
    outline: none !important;
    border: none !important;
    box-shadow: none !important;
}
.noBack:active, .noBack:hover,.noBack:target,.noBack:checked{outline: none;border: none;box-shadow: none;}
.dark {
    color: #000;
}
.purple {
    color: rebeccapurple !important;
}

.round{border-radius: 10px;color: rgb(12, 108, 242) !important;font-size: 1.2em;font-weight: 700;}