.connect i {vertical-align:bottom;font-size:1.6em;border-radius: 10px;background: rgb(12, 108, 242);padding:9px 12px;display: inline;color: #fff;margin-right: 10px;margin-bottom: -50px !important;}
.connect p {vertical-align:top;border-radius: 10px;border: 2px solid rgb(12, 108, 242);padding:10px 20px 10px 0;display: inline;color:rgb(18, 29, 51);margin-left: 20px;font-size: 1.1em;}
@media (max-width: 1500px){
    .connect{font-size: .7em;text-align: right;}
        .connect p{padding: 10px 10px;margin-left: 0;background: rgb(12, 108, 242);color: #fff;}
        .connect i{display: none;}
}