.InputDefine{
    border: 1px solid lightgrey;
    outline: none;
    padding: 15px 20px;
    font-weight: 600;
    box-shadow: inset 2px -0px lightgrey;
    width: 100%;
    background-color: aliceblue;
    border-radius: 10px;
}
input[type="file"] {
    opacity: 0;
    position: absolute;
    z-index: -1;
}
.fa-plusss{
    background: transparent;
    border:2px dashed lightgrey;
    width:100% !important;
    height:100% !important;
    padding:4px 10px;
    margin: 10px;
    cursor: pointer;
}
label{margin-right: 10px;padding: 0 !important;}
.w-100 {
    width: 100% !important;
}

.w-50 {
    width: 50% !important;
}

.w-90 {
    width: 90% !important;
}

.w-80 {
    width: 80% !important;
}.w-70 {
    width: 70% !important;
}.w-60 {
    width: 60% !important;
}